//import { SHOW_LIST } from './../actions/index';


let initialState = {
  posts: []
};

const postReducer = (state= initialState, action) => {
  switch(action.type) {
    case 'LIST_POSTS':
      return {
        ...state,
        posts: action.payload
      }
    case "ADD_POST":
      return {
        ...state, 
        posts: [...state.posts, action.payload]
      }
    default: 
      return {
        state
      }
  }
};

export default postReducer;